		async bindLogin() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			const {
				username,
				password
			} = this;
			if (username.length < 3) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 3 个字符'
				});
				return;
			}
			if (password.length < 6) {
